<!-- 添加车辆 -->
<template>
	<view>
		<view class="box1">
			<view class="input">
				<view class="t1">车型信息</view>
				<view class="t2">
					<input type="text" v-model="carType" placeholder="请输入车型信息(例:比亚迪白色)" style="width: 450rpx;">
					<!-- <picker mode="selector" :range="carList" @change="bindPickerChange()">
						<view>{{chooseCar}}</view>
					</picker> -->
				</view>
				<image :src="cdn+'/icon/triangle.svg'" mode="aspectFill"></image>
			</view>
			<view class="input">
				<view class="t1">车牌号码</view>
				<view class="t2" @click="input">
					<input type="text" v-model="province" class="font" placeholder-style="color:#fff">
					<input type="text" placeholder="请输入车牌号(左边输入省份)" v-model="carId" maxlength="6" style="width: 400rpx;">
				</view>
			</view>
		</view>
		<view class="box2">
			<view class="son" v-for="(k,i) in msgList" :key="i">
				<view class="up">
					<view class="name">
						<image :src="cdn+'/coachSection/icon/'+k.pic" mode="aspectFill"></image>
						<text>{{k.name}}</text>
					</view>
					<view class="num" @click="$win.show('record/record?name='+k.name)">
						<text>共{{k.num}}条</text>
						<image src="/static/images/icon/arrow-right-bold.png" mode="aspectFill"></image>
					</view>
				</view>
				<view class="mid">
					{{k.content}}
				</view>
				<view class="down" @click="$win.show(k.link)">
					<image src="/static/images/icon/add.svg" mode="aspectFill"></image>
					<view>记录</view>
				</view>
			</view>
		</view>

		<button class="sub" @click="addCar()" :disabled="carId==null||carType==null||province==''">确定</button>
	</view>
</template>

<script>
	import {
		mapActions,
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				cdn: this.$conf.CDN_HOST,
				province:'粤',
				carId:null,
				carList: ['car1', 'car2'],
				chooseCar: '请选择车型信息',
				carType:null,
				msgList: [{
						pic: '/insurance.png',
						name: '保险',
						num: 1,
						content: '--到期',
						link: 'insure/insure',
					},
					{
						pic: 'maintenance.png',
						name: '保养',
						num: 1,
						content: '--元',
						link: 'maintain/maintain',
					},
					{
						pic: 'accident.png',
						name: '出险',
						num: 1,
						content: '--次',
						link: 'accident/accident',
					},
					{
						pic: 'fix.png',
						name: '维修',
						num: 1,
						content: '--元',
						link: 'fix/fix',
					}
				]
			}
		},
		methods: {
			...mapActions({
				addCar_fun:'coachSection/addCar',
				
			}),
			bindPickerChange(e) {
				console.log(e)
			},
			input(){
				
			},
			async addCar(){
				let carId=this.province+this.carId
				if(carId.length<7){
					this.$win.nlog('车牌号位数填写有误')
					return
				}
				let data={
				userId:this.$db.getSync('userId'),
				carType:this.carType,
				carId,
				}
				let res=await this.addCar_fun(data)
			}
		},
		computed:{
			...mapState({
				
			})
		},
		onShow() {
			
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f2f3f5;
	}

	.box1 {
		display: flex;
		flex-direction: column;
		align-items: center;
		// margin-top: 128rpx;
		width: 750rpx;
		height: 290rpx;
		box-sizing: border-box;
		padding: 40rpx 0 10rpx;
		background: #ffffff;

		.input {
			position: relative;
			display: flex;
			flex-direction: row;
			align-items: center;
			width: 690rpx;
			height: 90rpx;
			margin-bottom: 30rpx;
			background: #f2f2f2;
			border-radius: 8rpx;
			box-sizing: border-box;
			padding: 0 30rpx;

			.t1 {
				margin-right: 30rpx;
				font-size: 28rpx;
				font-family: Source Han Sans CN, Source Han Sans CN-Regular;
				font-weight: 400;
				text-align: left;
				color: #666666;
			}

			.t2 {
				display: flex;
				align-items: center;
				// justify-content: space-between;
				width: 450rpx;
				height: 90rpx;
				font-size: 28rpx;
				font-family: Source Han Sans CN, Source Han Sans CN-Regular;
				font-weight: 400;
				text-align: left;
				color: #999999;
				
				.font{
					position: relative;
					background: #29c563;
					border-radius: 2rpx;
					text-align: center;
					width: 46rpx;
					height: 46rpx;
					box-sizing: border-box;
					font-size: 32rpx;
					line-height: 46rpx;
					font-family: Source Han Sans CN, Source Han Sans CN-Regular;
					font-weight: 400;
					color: #fff;
					margin-right: 10rpx;
					&::after{
						position: absolute;
						bottom: 0;
						right:  0;
						width: 0;
						height: 0;
						content: '';		
						border-bottom: 7rpx solid #000;
						border-right: 7rpx solid #000;
						border-top: 7rpx solid #29c563;
						border-left: 7rpx solid #29c563;
					}
				}
				
				picker {
					width: 100%;
					height: 100%;

					view {
						line-height: 90rpx;
					}
				}

			}

			image {
				position: absolute;
				right: 30rpx;
				width: 25rpx;
				height: 18rpx;
			}
		}
	}

	.box2 {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;
		width: 750rpx;
		box-sizing: border-box;
		padding: 30rpx 30rpx 0;

		.son {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: space-around;
			min-width: 330rpx;
			min-height: 312rpx;
			box-sizing: border-box;
			padding: 0 24rpx;
			background-color: #ffffff;
			border-radius: 16rpx;
			margin-bottom: 30rpx;

			.up {
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				width: 100%;

				.name {
					display: flex;
					flex-direction: row;
					align-items: center;

					image {
						margin-right: 10rpx;
						width: 38rpx;
						height: 38rpx;
						// background: #37ca6e;
					}

					text {
						font-size: 30rpx;
						font-family: Source Han Sans CN, Source Han Sans CN-Bold;
						font-weight: 700;
						text-align: left;
						color: #333333;
					}
				}

				.num {
					display: flex;
					flex-direction: row;
					align-items: center;

					text {
						line-height: 24rpx;
						font-size: 24rpx;
						font-family: Source Han Sans CN, Source Han Sans CN-Regular;
						font-weight: 400;
						text-align: right;
						color: #666666;
					}

					image {
						width: 17rpx;
						height: 17rpx;
						// background-color: #666666;
					}
				}
			}

			.mid {
				font-size: 26rpx;
				font-family: Source Han Sans CN, Source Han Sans CN-Regular;
				font-weight: 400;
				text-align: center;
				color: #333333;
			}

			.down {
				display: flex;
				flex-direction: row;
				align-items: center;
				justify-content: center;
				width: 200rpx;
				height: 60rpx;
				background: #e8fbef;
				border-radius: 30rpx;

				image {
					margin-right: 4rpx;
					width: 26rpx;
					height: 26rpx;
					// background: #29c563;
				}

				view {
					line-height: 30rpx;
					font-size: 30rpx;
					font-family: Source Han Sans CN, Source Han Sans CN-Regular;
					font-weight: 400;
					text-align: center;
					color: #29c563;
				}
			}
		}
	}


	.sub {
		margin-top: 70rpx;
		font-size: 32rpx;
		font-family: Source Han Sans CN, Source Han Sans CN-Regular;
		font-weight: 400;
		text-align: center;
		color: #ffffff;
		width: 580rpx;
		height: 90rpx;
		line-height: 90rpx;
		background: linear-gradient(134deg, #54d685, #29c563 99%);
		border-radius: 45rpx;
	}
</style>
